import { UseMoneyOption } from '@nequi/features/money-management/presentation';

export class ContingencyViewModel {
  public isLoadingBalance: boolean;
  public availableAmount: number;
  public totalAmount: number;
  public reloadBalance: boolean;
  public nickName: string;
  public isOriginMoneyOpened: boolean;
  public greeting: string;
  public shouldShowAvailable: boolean;
  private options: UseMoneyOption[];

  public get useMoneyOptions(): UseMoneyOption[] {
    return this.options;
  }

  public setUseMoneyOptions(options: UseMoneyOption[]) {
    this.options = options;
  }

  public validateGreeting(greeting: string) {
    if (!this.nickName) {
      this.greeting = greeting.slice(0, -1);

      return;
    }

    this.greeting = greeting;
  }
}
