import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ApplicationDataConfig } from '@config/application-data.config';
import { Colors, StatusBarService } from '@nequi/apps/base/mobile/config/features/commons/services';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { AccountType } from '@nequi/features/commons/core';
import {
  AppOptionsProvider,
  NavigatorProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import {
  UseMoneyComponent,
  UseMoneyStatus,
  UseMoneyStatusService
} from '@nequi/features/money-management/presentation';
import { ToastService } from '@nequi/ui';

import { HomeConfig } from '../home/home.config';
import { ContingencyViewModel } from './contingency.view-model';

const DEFAULT_EXECUTION_TIME = 500;

@Component({
  selector: 'app-backpack-contingency',
  templateUrl: './contingency.page.html',
  styleUrls: ['../home/home.page.scss', './contingency.page.scss'],
})
export class ContingencyPage implements OnInit, OnDestroy {
  @ViewChild(UseMoneyComponent) public useMoneyComponent: UseMoneyComponent;

  public viewModel = new ContingencyViewModel();
  public loadingNickname: boolean;
  public config = HomeConfig;
  public isSavingAccount: boolean;

  constructor(
    private userInfoProvider: UserInfoProvider,
    private navigatorProvider: NavigatorProvider,
    private appOptionsProvider: AppOptionsProvider,
    private statusBarService: StatusBarService,
    private translateProvider: TranslateProvider,
    private useMoneyStatusService: UseMoneyStatusService,
    private applicationDataInteractor: ApplicationDataInteractor,
    private toastService: ToastService
  ) {}

  public async ngOnInit() {
    this.viewModel.setUseMoneyOptions(this.appOptionsProvider.getUseMoney());
    this.statusBarService.changeStatusBarColor(Colors.MEDIANOCHE);
    await this.loadNickName();

    this.useMoneyStatusService.subscribe(
      (status) => (this.viewModel.isOriginMoneyOpened = status === UseMoneyStatus.OPENED)
    );

    this.isSavingAccount = this.userInfoProvider.getAccount()?.type === AccountType.SAVINGS;
  }

  public ionViewDidEnter() {
    setTimeout(() => {
      this.useMoneyComponent.onTapFab();
      this.useMoneyComponent.open(this.viewModel.useMoneyOptions);
    }, DEFAULT_EXECUTION_TIME);
  }

  public ngOnDestroy() {
    this.useMoneyStatusService.unsubscribe();
  }

  public async loadNickName() {
    try {
      this.loadingNickname = true;
      this.viewModel.nickName = await this.applicationDataInteractor.findValue(
        ApplicationDataConfig.backpack.nickName,
        '',
        this.userInfoProvider.getPhoneNumber()
      );
    } catch (error) {
      this.viewModel.nickName = '';
    }

    this.loadGreeting();
    this.loadingNickname = false;
  }

  public onOptionSelected(option) {
    this.navigatorProvider.navigateForward(option.url, {
      state: {
        contingencyAvailable: this.viewModel.availableAmount,
      },
    });
  }

  public async toggleAvailable() {
    this.viewModel.shouldShowAvailable = !this.viewModel.shouldShowAvailable;

    await this.applicationDataInteractor.save(
      this.config.applicationData.balanceVisibleStatus,
      this.viewModel.shouldShowAvailable
    );
  }

  public async showDisableAlert() {
    const message = this.translateProvider.get(this.config.i18n.disabledOption);

    await this.toastService.showInfo(message);
  }

  private loadGreeting() {
    const greeting = this.translateProvider.get(this.config.greeting);

    this.viewModel.validateGreeting(greeting);
  }
}
