import { BalanceEntity } from '@nequi/features/money-management/core';
import { UseMoneyOption } from '@nequi/features/money-management/presentation';
import { ToolEntity } from '@nequi/features/settings/core';

export class HomeViewModel {
  public isLoadingBalance: boolean;
  public availableAmount: number;
  public totalAmount: number;
  public reloadBalance: boolean;
  public nickName: string;
  public hasPocketsCreated: boolean;
  public dashboardToolList: Array<ToolEntity>;
  public isOriginMoneyOpened: boolean;

  private options: UseMoneyOption[];

  public get useMoneyOptions(): UseMoneyOption[] {
    return this.options;
  }

  public setUseMoneyOptions(options: UseMoneyOption[]): void {
    this.options = options;
  }

  public setBalanceData(balance: BalanceEntity): void {
    this.availableAmount = balance.available;
    this.totalAmount = balance.total;
    this.hasPocketsCreated = balance.pockets > 0;
    this.isLoadingBalance = false;
  }
}
