import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ApplicationDataConfig } from '@config/application-data.config';
import { DeepLinkService } from '@nequi/apps/base/mobile/config/features/commons/services/deep-link';
import {
  Colors,
  StatusBarService
} from '@nequi/apps/base/mobile/config/features/commons/services/status-bar';
import { AlternateFlowsInteractor } from '@nequi/features/alternate-flows/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { ToolType, ToolViewEntity } from '@nequi/features/closet/presentation';
import {
  AppOptionsProvider,
  PreventScreenshotProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons';
import {
  AccountType,
  AnalyticsInteractor,
  AnalyticsThirdPartyInteractor,
  AnalyticsType
} from '@nequi/features/commons/core';
import { RunOnce } from '@nequi/features/commons/decorators';
import { NavigatorProvider, SessionProvider } from '@nequi/features/commons/providers';
import {
  BalanceEvents,
  BalanceEventsHandler,
  BalanceInteractor
} from '@nequi/features/money-management/core';
import {
  GoalImageService,
  UseMoneyComponent,
  UseMoneyStatus,
  UseMoneyOption,
  UseMoneyStatusService
} from '@nequi/features/money-management/presentation';
import { NotificationInteractor } from '@nequi/features/notifications/core';
import { ProfileInteractor } from '@nequi/features/profile/core';
import { ProfileModel } from '@nequi/features/profile/data';
import { ProfileService, UpdateImageProfileService } from '@nequi/features/profile/presentation';
import { PushNotificationsService } from '@nequi/features/push-notifications/presentation';
import { RegistrationCompletedService } from '@nequi/features/security/biometry/presentation';
import { SettingInteractor, ToolEntity } from '@nequi/features/settings/core';
import { ToolListBottomSheetService } from '@nequi/features/settings/presentation';
import { ResourceId } from '@nequi/features/style-manager/core';
import { ToastService, TopToBottomCornerTransition } from '@nequi/ui';
import { DateFormat, transformDateFromString, getFormattedDateTimes } from '@nequi/utils/date';
import { createFormat } from '@nequi/utils/string';

import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

@Component({
  selector: 'app-backpack-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit, OnDestroy {
  @ViewChild(UseMoneyComponent) public useMoneyComponent: UseMoneyComponent;
  public viewModel = new HomeViewModel();
  public config = HomeConfig;
  public isVisible = true;

  public availableText: string;
  public optionKeyId: string;
  public resourceId: string;
  public shouldShowAvailable: boolean;
  public isSavingAccount: boolean;
  public hasNotifications: boolean;
  public userProfileImage: string;
  public loadingNickname: boolean;
  private balanceEvent: Function;

  constructor(
    private balanceInteractor: BalanceInteractor,
    private profileModel: ProfileModel,
    private profileService: ProfileService,
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private profileInteractor: ProfileInteractor,
    private clevertapInteractor: AnalyticsThirdPartyInteractor,
    private toastService: ToastService,
    private settingInteractor: SettingInteractor,
    private toolListBottomSheetService: ToolListBottomSheetService,
    private preventScreenshotProvider: PreventScreenshotProvider,
    private pushNotificationsService: PushNotificationsService,
    private sessionProvider: SessionProvider,
    private statusBarService: StatusBarService,
    private useMoneyStatusService: UseMoneyStatusService,
    private userInfoProvider: UserInfoProvider,
    private balanceEventsHandler: BalanceEventsHandler,
    private deepLinkService: DeepLinkService,
    private appOptionsProvider: AppOptionsProvider,
    private analyticsInteractor: AnalyticsInteractor,
    private applicationDataInteractor: ApplicationDataInteractor,
    private registrationCompletedService: RegistrationCompletedService,
    private notificationInteractor: NotificationInteractor,
    private goalImageService: GoalImageService,
    private updateImageProfileService: UpdateImageProfileService,
    private experimentsAlternateFlowsInteractor: AlternateFlowsInteractor
  ) {
    this.getBalance();

    this.balanceEvent = this.balanceEventsHandler.on(BalanceEvents.BALANCE_UPDATED, (balance) => {
      this.viewModel.setBalanceData(balance);
      this.setAvailableTextMessage();
    });
  }

  @RunOnce(ApplicationDataConfig.backpack.saveFullName)
  private async persistFullName(fullName: string) {
    if (fullName) {
      await this.applicationDataInteractor.save(
        ApplicationDataConfig.backpack.fullName,
        fullName,
        this.userInfoProvider.getPhoneNumber()
      );
    }
  }

  @RunOnce(ApplicationDataConfig.backpack.saveNickName)
  private async persistNickName(nickName: string) {
    if (nickName) {
      await this.applicationDataInteractor.save(
        ApplicationDataConfig.backpack.nickName,
        nickName,
        this.userInfoProvider.getPhoneNumber()
      );
    }
  }

  public async ngOnInit() {
    this.setResourceId();
    this.setAvailableTextMessage();
    this.viewModel.setUseMoneyOptions(this.appOptionsProvider.getUseMoney());
    this.statusBarService.changeStatusBarColor(Colors.MEDIANOCHE);
    this.registrationCompletedService.subscribe(() => this.showCoachmarkUseMoney());

    await this.getUserProfile();
    this.useMoneyStatusService.subscribe(
      (status) => (this.viewModel.isOriginMoneyOpened = status === UseMoneyStatus.OPENED)
    );

    this.updateImageProfileService.subscribe(() => this.loadImageFromStorage());
    this.profileService.subscribe(() => this.getUserProfile());
  }

  public async ionViewWillEnter() {
    this.preventScreenshotProvider.enable();
    await this.setDashboardTools();
    const didDeepLinkNavigation = await this.deepLinkService.navigatePendingDeepLink();

    if (!didDeepLinkNavigation) {
      await this.pushNotificationsService.processCurrentNotification(
        this.sessionProvider.userHasSession
      );
    }

    await this.setNotificationTabNotifications();
  }

  public ngOnDestroy() {
    this.useMoneyStatusService.unsubscribe();
    this.balanceEvent?.();
    this.updateImageProfileService.unsubscribe();
  }

  public async toggleAvailable() {
    this.shouldShowAvailable = !this.shouldShowAvailable;

    await this.applicationDataInteractor.save(
      this.config.applicationData.balanceVisibleStatus,
      this.shouldShowAvailable
    );
  }

  public openMoneyDetail() {
    this.isVisible = false;
    this.navigatorProvider.navigateRoot(this.config.routes.moneyPartition, {
      animated: true,
      animation: (baseEl, opts) => TopToBottomCornerTransition(baseEl, opts),
    });
  }

  public async getBalance(event?) {
    if (this.viewModel.isLoadingBalance) {
      this.setCompletePullToRefreshEvent(event);

      return;
    }

    try {
      this.viewModel.isLoadingBalance = true;
      this.shouldShowAvailable = await this.applicationDataInteractor.findValue(
        this.config.applicationData.balanceVisibleStatus,
        false
      );

      const balance = await this.balanceInteractor.getBalance();

      this.viewModel.setBalanceData(balance);
      this.setAvailableTextMessage();
      this.setCompletePullToRefreshEvent(event);
    } catch (error) {
      this.viewModel.isLoadingBalance = false;
      this.viewModel.reloadBalance = false;
      this.setCompletePullToRefreshEvent(event);
    }
  }

  public navigateTo(route: string) {
    this.navigatorProvider.navigateForward(route);
  }

  public async getUserProfile() {
    this.loadingNickname = true;

    const userProfileInfo = await this.profileInteractor.getUserProfile();

    this.profileModel.setUserProfileInfo(userProfileInfo);
    this.viewModel.nickName = this.userInfoProvider.nickName;
    this.loadingNickname = false;

    this.analyticsInteractor.initialize({ type: AnalyticsType.DYNATRACE });

    await this.saveFingerPrintOnLoginInLocalStorage();

    this.clevertapInteractor.identifyUser({
      name: this.userInfoProvider.nickName,
      phoneNumber: this.profileModel.phoneNumber,
      birthDate: transformDateFromString(
        this.profileModel.birthDate ?? '',
        DateFormat.YYYY_MM_DD_HH_MM_SS
      ),
      email: this.profileModel.email,
    });

    this.persistFullName(this.userInfoProvider.fullName);
    this.persistNickName(this.userInfoProvider.nickName);
    await this.loadImageFromStorage();
  }

  public cardClick(tool: ToolEntity) {
    if (!tool.url) {
      return false;
    }

    if (tool.analytics) {
      tool.analytics.forEach((event) => {
        this.analyticsInteractor.trackEvent(event);
      });
    }

    this.navigatorProvider.navigateForward([tool.url], { queryParams: tool.params, state: tool });
  }

  public async openBottomSheetToolList(event: TouchEvent, index: number, tool: ToolEntity) {
    event.preventDefault();

    if (!tool.moveOptions.canMove) {
      await this.toastService.showError(tool.moveOptions.dashboardCanNotMoveMessage);

      return;
    }

    try {
      const options = this.translateProvider.get(HomeConfig.changeToolMessages);
      const items = await this.settingInteractor.getClosetToolList();

      const { data } = await this.toolListBottomSheetService.show({
        title: createFormat(options['bottom-sheet'].title, [tool.title]),
        items: items
          .map((item: ToolEntity, count): ToolViewEntity => {
            return {
              ...item,
              index: count,
              type: ToolType.STORE,
            };
          })
          .filter((item) => item.moveOptions.canMove),
        successMessage: createFormat(options.success, [tool.title]),
        toolsIndex: { backpack: index },
      });

      if (data === true) {
        this.setDashboardTools();
      }
    } catch (error) {
      await this.toastService.showError(error.message);
    }
  }

  public async onOptionSelected(option: UseMoneyOption) {
    this.sendAnalyticsUseMoney(option.id);

    if (option.id === 'cash-out') {
      this.experimentsAlternateFlowsInteractor
        .getAlternateFlows()
        .then(() => {
          this.navigatorProvider.navigateForward('admin/experiment-alternate-flows');
        })
        .catch(() => {
          this.navigatorProvider.navigateForward(option.url);
        });
    } else {
      this.navigatorProvider.navigateForward(option.url);
    }
  }

  public async setNotificationTabNotifications() {
    let totalUnreadNotificationQuantity: number;

    try {
      totalUnreadNotificationQuantity =
        await this.notificationInteractor.getTotalUnreadNotificationQuantity();
    } catch (error) {
      totalUnreadNotificationQuantity = 0;
    }

    this.hasNotifications = totalUnreadNotificationQuantity > 0;
  }

  public openHelp() {
    this.navigatorProvider.navigateForward([this.config.routes.help], {
      queryParams: {
        context: this.config.helpContext,
      },
    });
  }

  private getAvailableTextKey(hasPocketsCreated: boolean) {
    return hasPocketsCreated ? this.config.availableTextPockets : this.config.availableText;
  }

  private async setAvailableTextMessage() {
    const textKey = this.getAvailableTextKey(this.viewModel.hasPocketsCreated);

    this.availableText = this.translateProvider.get(textKey);
  }

  private setCompletePullToRefreshEvent(event) {
    if (event) {
      event.target.complete();
    }
  }

  private async setDashboardTools() {
    try {
      const tools = await this.settingInteractor.getBackpackToolList();

      this.viewModel.dashboardToolList = tools;
    } catch (error) {
      this.viewModel.dashboardToolList = HomeConfig.defaultTools;
    }
  }

  private async saveFingerPrintOnLoginInLocalStorage() {
    const fingerPrintBiometry = this.profileService.getFingerPrintBiometry();

    this.profileService.saveFingerprintOnLoginInLocalStorage(fingerPrintBiometry?.enabled);
  }

  private setResourceId() {
    this.resourceId =
      this.userInfoProvider.getAccount()?.type === AccountType.SAVINGS
        ? ResourceId.BACKPACK_WITHOUT_CEILING
        : ResourceId.BACKPACK;

    this.isSavingAccount = this.resourceId === ResourceId.BACKPACK_WITHOUT_CEILING;
  }

  private showCoachmarkUseMoney() {
    this.useMoneyComponent.showCoachmark();
  }

  private async loadImageFromStorage() {
    try {
      const optimizedImage = await this.goalImageService.get(this.config.userImageId);

      if (optimizedImage) {
        this.userProfileImage = optimizedImage;
      }
    } catch (error) {
      this.loadingNickname = false;
    }
  }

  private async sendAnalyticsUseMoney(id: string) {
    const analytics = this.config.analytics.useMoney;
    const action = this.config.analytics.useMoney.tags[id];

    if (!action) {
      return;
    }

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.name,
        eventProps: stringsArrayToObjectWithValues(analytics.params, [
          getFormattedDateTimes(),
          action,
        ]),
      },
    });
  }
}
